<template>
    <div class="whole-features-container">
        <h2 v-html="features.title"></h2>
        <div class="tabs-container flex-center">
            <a class="tablink" @click="openPage('facilities','defaultOpen')" id="defaultOpen" @load="click()" v-html="features.firstTab"></a>
            <a class="tablink" @click="openPage('excursions','excursionsTab')" id="excursionsTab" v-html="features.secondTab"></a>
            <a class="tablink" @click="openPage('interior','interiorTab')" id="interiorTab" v-html="features.thirdTab"></a>
        </div>
        <img class="right-arrow" src="../assets/right-arrow.png" alt="scroll-right"/>
        <div id="facilities" class="tabcontent max-width">
            <div class="pair-set" data-aos="fade-right" data-aos-delay="10">
                <img src="../assets/gallery/tab1-1 (2).jpg" alt="people-walking-on-mars" @click="openModal('/img/tab1-1 (2).58e2a578.jpg')">
                <div class="details-container flex-center">
                    <p class="number">03</p>
                    <p class="text-detail">Resorts de lujo</p>
                </div>
            </div>
            <div class="pair-set" data-aos="fade-right" data-aos-delay="100">
                <img src="../assets/gallery/tab1-2.jpg" alt="more-people-walking-on-mars" @click="openModal('/img/tab1-2.c58a1702.jpg')">
                <div class="details-container flex-center">
                    <p class="number">27</p>
                    <p class="text-detail">KM² de exploración libre</p>
                </div>
            </div>
            <div class="pair-set" data-aos="fade-right" data-aos-offset="350">
                <img src="../assets/gallery/tab1-3.jpg" alt="stations-on-mars" @click="openModal('/img/tab1-3.437bdd3e.jpg')">
                <div class="details-container flex-center">
                    <p class="number">450</p>
                    <p class="text-detail">Empleados a tu disposición</p>
                </div>
            </div>
        </div>    
        <div id="excursions" class="tabcontent max-width">
            <div class="pair-set" data-aos="fade-right" data-aos-delay="10">
                <img src="../assets/gallery/tab2-1.jpg" alt="olimpus-mount" @click="openModal('/img/tab2-1.9306211b.jpg')">
                <div class="details-container">
                    <h4 class="title">Monte Olimpo</h4>
                    <p class="subtitle">Con 21 km de alto, es el pico más alto del sistema solar. Ofrecemos caminatas diarias y tramos a la cima. Reservá <span>acá</span></p>
                </div>
            </div>
            <div class="pair-set" data-aos="fade-right" data-aos-delay="100">
                <img src="../assets/gallery/tab2-2.jpg" alt="omega-glaciar" @click="openModal('/img/tab2-2.a0e87f35.jpg')">
                <div class="details-container">
                    <h4 class="title">Glaciar Omega X-26</h4>
                    <p class="subtitle">Quizás seas uno de los afortunados en presenciar uno de los 3 derrumbes anuales de este glaciar. Reservá <span>acá</span></p>
                </div>
            </div>
            <div class="pair-set" data-aos="fade-right" data-aos-delay="350">
                <img src="../assets/gallery/tab2-3.jpg" alt="great-red-crater" @click="openModal('/img/tab2-3.618ea47e.jpg')">
                <div class="details-container">
                    <h4 class="title">Gran Cráter Rojo</h4>
                    <p class="subtitle">Más de 5 km de diámetro y 7 km de profundidad lo transforman en uno de los cráteres más impresionantes de Marte. Reservá <span>acá</span></p>
                </div>
            </div>
        </div>   
        <div id="interior" class="tabcontent max-width">
            <div class="pair-set" data-aos="fade-right" data-aos-delay="10">
                <img src="../assets/gallery/tab3-3.jpg" alt="great-views" @click="openModal('/img/tab3-3.123e6fdf.jpg')">
                <div class="details-container">
                    <h4 class="title">Increíbles vistas</h4>
                    <p class="subtitle">Disfrutá de los mejores paisajes y una sala de con vistas 360°.</p>
                </div>
            </div>
            <div class="pair-set" data-aos="fade-right" data-aos-delay="100">
                <img src="../assets/gallery/tab3-2.jpg" alt="comfortable-ship" @click="openModal('/img/tab3-2.ea50b5b0.jpg')">
                <div class="details-container">
                    <h4 class="title">Comodidad asegurada</h4>
                    <p class="subtitle">Contamos con bar y piscina incluídos tanto en la nave como en las estaciones marcianas. </p>
                </div>
            </div>
            <div class="pair-set" data-aos="fade-right" data-aos-delay="350">
                <img src="../assets/gallery/tab3-1.jpg" alt="premium-service" @click="openModal('/img/tab3-1.8ab05c1a.jpg')">
                <div class="details-container">
                    <h4 class="title">Atención premium</h4>
                    <p class="subtitle">Nuestro personal altamente capacitado se encargará de que tengas una experiencia a la altura de lo que merecés.</p>
                </div>
            </div>
        </div>    
    </div>
</template>

<script>
export default{
    name: 'Features',
    data(){
        return{
            features:{
                title:"ACERCA DE ELYSIUM PLANITIA",
                firstTab:"ESTABLECIMIENTO",
                secondTab:"EXCURSIONES",
                thirdTab:"INTERIOR"

            }
        }
    },
    methods:{
        openPage(pageName, tabName) {
            let i, tabcontent, tablinks;
            tabcontent = document.getElementsByClassName("tabcontent");
            for (i = 0; i < tabcontent.length; i++) {
                tabcontent[i].style.display = "none";
                
            }
            // Show the specific tab content
            document.getElementById(pageName).style.display = "grid";
            
            tablinks = document.getElementsByClassName("tablink");
            for (i = 0; i < tablinks.length; i++) {
                tablinks[i].style.borderBottom = "none";
            }
            document.getElementById(tabName).style.borderBottom = "2px solid white";
        },
        openModal(src){
            let facContainer = document.querySelector('#facilities')
            let excContainer = document.querySelector('#excursions')
            let intContainer = document.querySelector('#interior')
            let allContainers = document.querySelectorAll('.tabcontent')
            let modal = document.createElement('div')
            let img = document.createElement('img')
            let cross = document.createElement('img')
            
            cross.src = `${require('../assets/cross.svg')}`
            img.src = src

            modal.setAttribute('class', "modal")
            img.setAttribute('class', "modal-photo")
            cross.setAttribute('class', "cross")

            if(src.includes('tab1')){
                facContainer.appendChild(modal)
                modal.appendChild(img)
                modal.appendChild(cross)
                cross.onclick = () => {facContainer.removeChild(modal)}
            } 
            else if(src.includes('tab2')){
                excContainer.appendChild(modal)
                modal.appendChild(img)
                modal.appendChild(cross)
                cross.onclick = () => {excContainer.removeChild(modal)}
            } else if(src.includes('tab3')){
                intContainer.appendChild(modal)
                modal.appendChild(img)
                modal.appendChild(cross)
                cross.onclick = () => {intContainer.removeChild(modal)}
            }
                
        }
    }
}
</script>

<style scoped>
.whole-features-container{
    flex-direction:column;
    background: url("../assets/stars-background.jpg") no-repeat;
    background-position: center;
    background-size: cover;
    widows: 100%;
    overflow: hidden;
    flex-direction: column;
    position: relative;
    padding:3rem 0;
}
.whole-features-container:before{
    content:'';
    position:absolute;
    width: 100%;
    height:200px;
    background:var(--transToBlack-background);
    bottom:0;
    z-index: var(--z-normal);
}
.whole-features-container:after{
    content:'';
    position:absolute;
    width: 100%;
    height:200px;
    background:var(--transToBlack-background);
    top:0;
    transform:rotate(180deg);
}

.whole-features-container h2{
    font-size: 1.3rem;
}
.tabs-container a{
    font-size: 0.8rem;
    margin: 2rem 0.5rem;
    cursor:pointer
}
.tabs-container a:nth-child(1){
    border-bottom: 2px solid white
}
.whole-features-container h2,
.tabs-container{
    position:relative;
    z-index: var(--z-modal);
    text-align:center
}
.right-arrow{
    width:1.5rem;
    animation:scrollRight 2s infinite;
    position:relative;
    left:50%;
    transform:translateX(-50%)
}
#excursions,
#interior{
    display:none
}
.tabcontent{
    width: 100%;
    overflow: auto;
    display: grid;
    grid-template-columns:repeat(3,minmax(auto, auto));
    grid-template-rows: minmax(10rem,auto);
    padding:1rem 2rem;
    z-index: var(--z-modal);
    position:relative;
    left:50%;
    transform:translateX(-50%);
}
/*establecimiento*/
.tabcontent img{
    height:15rem;
    width:25rem;
    left:50%;
    transform:translateX(-50%);
    position:relative;
    cursor:pointer;
    transition: all .5s
}
.tabcontent img:hover{
    filter:brightness(90%)
}
.pair-set .details-container{
    flex-direction:column;
    position:relative;
}
.details-container .number{
    color:var(--main-color);
    font-size: 8rem;
    font-family: Sansation;
}
.details-container .text-detail{
    font-size: 2rem;
    position:absolute;
    margin-top:5rem;
    width:15rem;
    text-align:center;
}
.details-container .subtitle span{
    color: var(--main-color);
    font-family: Sansation;
    cursor:pointer;
}
/*excursiones e interior*/
#excursions .details-container,
#interior .details-container{
    text-align:left
}
#excursions .title,
#interior .title{
    font-size: 1.5rem;
    margin: 1rem auto;
}
#excursions .subtitle,
#interior .subtitle{
    font-size: 1.1rem;
    width: 80%;
}
@media(min-width:700px){
    .right-arrow{
        display:none
    }
}
@media(min-width:1300px){
    .tabcontent::-webkit-scrollbar{
        height:0;
    }
}


@keyframes scrollRight{
    0%{
        transform:translateX(-0.5rem)
    }
    50%{
        transform:translateX(0.5rem)
    }
    100%{
        transform:translateX(-0.5rem)
    }
}
</style>