<template>
    <div class="whole-climate-container">
        <h2 v-html="climate.title"></h2>
        <div v-show="getWeather" class="climate-container flex-center">
            <div class="temp flex-center" data-aos="fade-right" data-aos-delay="10">
                <p class="min-temp"></p>
                <p class="weather-text">TEMPERATURA MÍNIMA</p>
            </div>
            <div class="temp flex-center" data-aos="fade-right" data-aos-delay="30">
                <p class="max-temp"></p>
                <p class="weather-text">TEMPERATURA MÁXIMA</p>
            </div>
            <div class="temp flex-center" data-aos="fade-right" data-aos-delay="50">
                <p class="wind-speed"></p>
                <p class="weather-text">VIENTO</p>
            </div>
            <div class="temp flex-center" data-aos="fade-right" data-aos-delay="70">
                <p class="pressure"></p>
                <p class="weather-text">PRESIÓN ATM.</p>
            </div>
            <div class="temp flex-center" data-aos="fade-right" data-aos-delay="90">
                <p class="clouds"></p>
                <p class="weather-text">NUBOSIDAD</p>
            </div>
            <span class="message" style="text-align:center"></span>
        </div>
        <div class="extra-info">
            <span class="source">Fuente: Mars Insight</span>
        </div>
    </div>
</template>

<script>
import axios from "axios"
export default{
    name: 'Climate',
    data(){
        return{
            climate:{
                title:"CLIMA EN ELYSIUM PLANITIA"
            }
        }
    },
    mounted(){
        this.getWeather()
    },
    methods: {
        getWeather(){
            let source = document.querySelector('.source')
            let minTemp = document.querySelector(".min-temp")
            let maxTemp = document.querySelector(".max-temp")
            let windSpeed = document.querySelector(".wind-speed")
            let pressure = document.querySelector(".pressure")
            let clouds = document.querySelector(".clouds")
            let warning = document.querySelector(".message")

            axios
                .get('https://api.nasa.gov/insight_weather/?api_key=DEMO_KEY&feedtype=json&ver=1.0')
                    .then(response => {
                        let data = Object.values(response)[0]
                        let day = Object.values(data)[4]
                        // console.log(response)
                        // console.log(day)
                        minTemp.innerHTML = `
                        ${Math.round(day.AT.mn)}°C / ${Math.round((day.AT.mn * 9/5) + 32) - 50}°F
                        `
                        maxTemp.innerHTML = `
                        ${Math.floor(day.AT.mx)}°C / ${Math.floor((day.AT.mx * 9/5) + 32) - 23}°F
                        `
                        windSpeed.innerHTML = `
                        ${Math.floor(day.HWS.mx)} / ${day.WD[0].compass_point}
                        `
                        pressure.innerHTML = `
                        ${Math.floor(day.PRE.av)} Pa
                        `

                        clouds.innerHTML = `
                        ${(Math.ceil(Math.random() * 100))}%
                        `
                    })
                    .catch( err => {
                        console.log(err)
                        console.log(`*If you are a recruiter checking my API Call, have in mind that this particular API 
                                    is not well mantained despite being owned by NASA. The key works correctly and is well used, 
                                    but from time to time it goes off. Please come back a few days ago and hopefully it will be working.
                                    You can check the official page with the same key here https://mars.nasa.gov/insight/weather/
                                    If you prefer you can check my other API calls in my Github page 
                                    https://github.com/avenali5/Studio-Ghibli-API
                                    https://github.com/avenali5/axiosdolar`)
                     
                        warning.style.display = "block"
                        warning.innerHTML = 'No pudimos conectarnos con los datos climáticos, vuelva en breve.'
                        weatherVals.style.display = "none"
                    });
    }          
}
}
</script>

